//Link -    ,         
import { Link } from 'react-router-dom'
import { useAuth } from './security/AuthContext'


//   
function HeaderComponent(){

    //import AuthContext  hook
    // const authContext = useContext(AuthContext)
    // useContext Hook 
    const authContext = useAuth()
    // context boolean 
    const isAuthenticated = authContext.isAuthenticated
    //  username 
    const username = authContext.username

    function logout() {
        //    AuthContext logout 
        authContext.logout()
    }
    
    //console.log(authContext)
    //console.log(authContext.number)

    return(
        // border-bottom:    ,border-light:     ,border-5:   5px 
        // mb-5:    (5) ,p-2:     2 ( 0.5rem) 
               <header className="border-bottom border-light border-5 mb-5 p-2">
                {/* Bootstrap      */}
               <div className="container">
                   <div className="row">
                   {/* navbar: Bootstrap    .        
                    navbar-expand-lg: (large)       .
                             */}
                       <nav className="navbar navbar-expand-lg">
                       {/* navbar-brand:        ,ms-2:   ()  2  
                        fs-2:   2  Bootstrap      
                        fw-bold:   (bold) ,text-black:    . */}
                           <a className="navbar-brand ms-2 fs-2 fw-bold text-black" href="http://13.230.93.73:8080/v1/index.html">Yoon's Library-App Link</a>
                           <div className="collapse navbar-collapse">
                           {/* navbar-nav:         
                           ,nav-item:       .
                            fs-5:   5   Bootstrap    */}
                               <ul className="navbar-nav">
                   {/*     Link,to */}
                                   <li className="nav-item fs-5">
                                   {/* JSX  isAuthenticated true Home   */}
                                        {isAuthenticated && <Link className="nav-link" to={`/welcome/${username}`}>Home</Link>}
                                    </li>
                                   <li className="nav-item fs-5">
                                   {/* JSX  isAuthenticated true Todos   */}
                                        {isAuthenticated && <Link className="nav-link" to="/todos">Todo </Link>}
                                    </li>
                               </ul>
                           </div>
                           <ul className="navbar-nav">
                   {/*     Link,to */}
                                {/* nav-link:          */}
                               <li className="nav-item fs-5">
                                   {/* JSX  isAuthenticated false Login   */}
                                    {!isAuthenticated && <Link className="nav-link" to="/login">Login</Link>}
                                </li>
                               <li className="nav-item fs-5">
                                   {/* JSX  isAuthenticated false Logout   */}
                                    {isAuthenticated && <Link className="nav-link" to="/logout" onClick={logout}>Logout</Link>}
                                </li>
                           </ul>
                       </nav>
                   </div>
               </div>
           </header>
   
    )
}

export default HeaderComponent