import './TodoApp.css'
import LogoutComponent from './LogoutComponent'
import HeaderComponent from './HeaderComponent'
import ListTodosComponent from './ListTodosComponent'
import ErrorComponent from './ErrorComponent'
import WelcomeComponent from './WelcomeComponent'
import LoginComponent from './LoginComponent'
import TodoComponent from './TodoComponent'
//react-router-dom   import
//BrowserRouter: HTML5 history API  UI URL   .
//Route:      .
//Switch:  Route     Route   . (React Router v5 )
//Routes:  Route  ,  Route  . (React Router v6 )
import { BrowserRouter, Navigate, Route, Routes} from 'react-router-dom'
import AuthProvider, { useAuth } from './security/AuthContext'

//   AuthenticatedRoute   children  
function AuthenticatedRoute({children}) {
    const authContext = useAuth()
    //   ture   
    if (authContext.isAuthenticated) {
        return children
    }

    //  URL   
    //root LoginComponent  
    return <Navigate to='/' />
}

export default function TodoApp(){
    return(
        <div className="TodoApp">
            {/*       */}
            <AuthProvider>
                <BrowserRouter>
                    {/*     */}
                    {/*  Link        */}
                    <HeaderComponent/>
                    <Routes>
                        <Route >
                            {/* url path     js  ''  */}
                            <Route path='/' element={ <LoginComponent /> }/>
                            <Route path='/login' element={ <LoginComponent /> }/>
                            {/* github pages URL   */}
                            <Route path='/flyio-client/' element={ <LoginComponent /> }/>

                            {/* welcome/ Url  username WelcomeComponent     */}
                            <Route path='/welcome/:username' element={
                                //     
                                 <AuthenticatedRoute>
                                     <WelcomeComponent/> 
                                 </AuthenticatedRoute>
                                 }/>
                            
                            {/*  url /todos ListTodosComponent  */}
                            <Route path='/todos' element={
                                //     
                                <AuthenticatedRoute>
                                    <ListTodosComponent/>
                                </AuthenticatedRoute>
                                }/>

                            {/*  url /todos/:id( segment) TodoComponent  */}
                            <Route path='/todo/:id' element={
                                //     
                                <AuthenticatedRoute>
                                    <TodoComponent/>
                                </AuthenticatedRoute>
                                }/>
                            {/*  url /logout LogoutComponent  */}
                            <Route path='/logout' element={
                                //     
                                <AuthenticatedRoute>
                                    <LogoutComponent/>
                                </AuthenticatedRoute>
                            }/>

                            {/*  url url error  */}
                            <Route path='*' element={<ErrorComponent/>}/>
                        </Route>
                    </Routes>
                </BrowserRouter>
            </AuthProvider>
        </div>
    )
}















