import { useEffect, useState } from "react";
import {
  deleteSpecificTodoApi,
  getTodosByUsernameApi,
} from "./api/TodoApiService";
import { useAuth } from "./security/AuthContext";
import { useNavigate } from "react-router-dom";

//  
function ListTodosComponent() {
  // 
  // const today = new Date();
  //   
  const authcontext = useAuth();
  // username 
  const username = authcontext.username;
  //   useNavicate 
  const navigate = useNavigate();

  // const targetDate = new Date(
  //   today.getFullYear(),
  //   today.getMonth(),
  //   today.getDay()
  // );
  // List<Todo>  api   Hook
  const [todos, setTodos] = useState([]);
  // Todo  api     message  Hook
  const [message, setMessage] = useState(null);

  // const todos = [
  //                 // {id:1, description:'Learn JavaScript', done:false, targetDate:targetDate},
  //                 // {id:2, description:'Learn SpringClould', done:false, targetDate:targetDate},
  //                 // {id:3, description:'Learn React', done:false, targetDate:targetDate}
  //                 ]

  // api  List<Todo>   
  //ListTodosComponent   refreshTodos() 
  //   []       
  //    useEffect      1 refreshTodos() 
  useEffect(() => {
    refreshTodos();
  }, []);

  function refreshTodos() {
    //URL ${username}   api 
    getTodosByUsernameApi(username)
      .then((response) =>
        //api    useState  
        {
          console.log(response.data);
          setTodos(response.data);
        }
      )
      .catch((error) => console.log(error));
  }

  //   
  function deleteTodo(id) {
    console.log("clicked" + id);
    //URL ${username}, ${id}   api 
    deleteSpecificTodoApi(username, id)
      .then(
        //   api    1. {message}  2. 
        () => {
          setMessage(`id = ${id} Todo Delete Success!`);
          refreshTodos();
        }
      )
      .catch((error) => console.log(error));
  }

  // Update   
  function updateTodo(id) {
    console.log("clicked" + id);
    // id   
    navigate(`/todo/${id}`);
  }

  //Add New Todo   
  function addNewTodo() {
    //URL {id}  -1()    TodoConponet 
    navigate(`/todo/-1`);
  }

  return (
    //  - container
    <div className="container">
      <h1> </h1>
      {/*  {message} null    */}
      {message && <div className="alert alert-warning">{message}</div>}
      <div>
        {/*   */}
        <table className="table">
          <thead>
            <tr>
              {/* bold   <th/>  */}
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {/*      {} */}
            {
              // todos      
              todos.map(
                // todos    todo    map   
                (todo) => (
                  //      id 
                  <tr key={todo.id}>
                    <td>{todo.description}</td>
                    {/* boolean  'Yes'  'No'    */}
                    <td>{todo.done ? "Yes" : "No"}</td>
                    {/*         */}
                    {/* <td>{todo.done.toString()}</td> */}
                    {/*         */}
                    {/* <td>{todo.targetDate.toDateString()}</td> */}
                    {/* api    */}
                    <td>{todo.targetDate.toString()}</td>
                    {/* bootstrap   */}
                    <td>
                      <button
                        className="btn btn-warning"
                        onClick={() => deleteTodo(todo.id)}
                      >
                        
                      </button>
                    </td>
                    {/* bootstrap   */}
                    <td>
                      <button
                        className="btn btn-success"
                        onClick={() => updateTodo(todo.id)}
                      >
                        
                      </button>
                    </td>
                  </tr>
                )
              )
            }
          </tbody>
        </table>
      </div>
      <div className="btn btn-success m-5" onClick={addNewTodo}>
          
      </div>
    </div>
  );
}

export default ListTodosComponent;
