import { useNavigate, useParams } from "react-router-dom";
import {
  createTodoApi,
  getSpecificTodoApi,
  updateTodoApi,
} from "./api/TodoApiService";
import { useAuth } from "./security/AuthContext";
import { useEffect, useState } from "react";
import { ErrorMessage, Field, Form, Formik } from "formik";
import moment from "moment";

export default function TodoComponent() {
  // <Route path='/todo/:id' - id    useParams Hook
  const { id } = useParams();
  //  
  const authContext = useAuth();
  //  Hook
  const navigate = useNavigate();
  //   username
  const username = authContext.username;
  //  
  const [description, setDescription] = useState("");
  // 
  const [targetDate, setTargetDate] = useState("");
  // 
  const [done, setDone] = useState("");

  //TodoComponet   getSpecificTodo() 
  //  [id]  getSpecificTodo()  
  useEffect(() => {
    //getSpecificTodo    
    //   () 
    getSpecificTodo();
  }, [id]);

  // Todo  api    update  (useEffect)
  function getSpecificTodo() {
    //Url {id} -1   Todo   Todo api  
    if (id != -1) {
      getSpecificTodoApi(username, id)
        .then((response) => {
          console.log(response);
          //  api description  set
          setDescription(response.data.description);
          //  api targetDate  set
          setTargetDate(response.data.targetDate);
          // API  boolean  "Yes"  "No"  
          setDone(response.data.done ? "Yes" : "No");
        })
        .catch((error) => console.log(error));
    }
  }
  //Todo  api   (save  )
  function onSubmit(values) {
    console.log(values);
    const todo = {
      id: id,
      username: username,
      //     name  
      description: values.description,
      targetDate: values.targetDate,
      //    "Yes"  true,  (, "No")  false  API 
       //   ES5 ,  
      done: values.done === "Yes",
    };
    console.log(todo);
    //Url {id} -1 (Add New Todo)
    if (id == -1) {
      //Post Request
      createTodoApi(username, todo)
        .then((response) => {
          console.log(response);
          //api   useNavigate Hook  
          navigate("/todos");
        })
        .catch((error) => console.log(error));
        //Url {id} -1 
    } else {
      // Put Request
      updateTodoApi(username, id, todo)
        .then((response) => {
          console.log(response);
          //api   useNavigate Hook  
          navigate("/todos");
        })
        .catch((error) => console.log(error));
    }
  }
  //formik   
  function validate(values) {
    //  
    let errors = {
      // description: "Enter a vaild description" ,
      // targetDate: "Enter a valid targetDate"
    };
    //   5   
    if (values.description.length < 5) {
      errors.description = "Enter at least 5 characters";
    }
    //  null       
    if (values.targetDate === null || values.targetDate === "" || !moment(values.targetDate).isValid()) {
      errors.targetDate = "Enter a targetDate";
    }
    console.log(values);
    return errors;
  }

  return (
    //bootstrap container
    <div className="container">
      <h1>Enter Todo Details</h1>
      <div>
        {/* api       name=""   */}
        {/* initialValues        */}
        {/* Save   onSubmit   */}
        {/*   submit    */}
        {/*       */}
        {/*       */}
        <Formik
          initialValues={{ description, targetDate, done }}
          enableReinitialize={true}
          onSubmit={onSubmit}
          validate={validate}
          validateOnChange={false}
          validateOnBlur={false}
        >
          {/* JSX   */}
          {() => (
            <Form>
              {/* description   */}
              <ErrorMessage
                name="description"
                //  div 
                component="div"
                className="alert alert-warning"
              />
              {/* targetDate   */}
              <ErrorMessage
                name="targetDate"
                //  div 
                component="div"
                className="alert alert-warning"
              />
              {/* bootstrap form-group , form-control */}
              <fieldset className="form-group">
                <label>Description</label>
                <Field
                  type="text"
                  className="form-control"
                  name="description"
                />
              </fieldset>
              <fieldset className="form-group">
                <label>Target Date</label>
                <Field type="date" className="form-control" name="targetDate" />
              </fieldset>
              {/*   'Is Done?'   */}
              <fieldset className="form-group">
                <label>Is Done?</label>
                {/* select,textarea  as   */}
                <Field as="select" className="form-control" name="done">
                  <option value="No">No</option>
                  <option value="Yes">Yes</option>
                </Field>
              </fieldset>
              <div>
                {/* formik  input onChange      */}
                <button className="btn btn-success m-5" type="submit">
                  Save
                </button>
              </div>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
