// username url    Http get Method Api 
export const getTodosByUsernameApi =
  //   parameter  ${username}  
  (username) => apiClient.get(`/users/${username}/todos`);
//http://localhost:8080/users/yoon/todos

// username, id url    Http delete Method Api 
export const deleteSpecificTodoApi =
  //   parameter  ${username}, ${id}  
  (username, id) => apiClient.delete(`/users/${username}/todos/${id}`);
//http://localhost:8080/users/yoon/todos/${id}

// username, id url  update   Todo  Http get Method Api 
export const getSpecificTodoApi =
  //   parameter  ${username}, ${id}  
  (username, id) => apiClient.get(`/users/${username}/todos/${id}`);
//http://localhost:8080/users/yoon/todos/${id}

// username, id url ,   save   Todo  Http PUT Method Api 
export const updateTodoApi =
  //   parameter  ${username}, ${id}  ,  @RquestBody  todo  
  (username, id, todo) => apiClient.put(`/users/${username}/todos/${id}`, todo);
//http://localhost:8080/users/yoon/todos/${id}

// username url ,   Add New Todo -> save    Todo  Http Post Method Api 
export const createTodoApi =
  //   parameter  ${username}  ,  @RquestBody  todo  
  (username, todo) => apiClient.post(`/users/${username}/todos`, todo);
//http://localhost:8080/users/yoon/todos
